.profile__info {
  margin-left: 30px;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
}
@media screen and (max-width: 1023px) {
  .profile__info {
    margin: 25px 0 0;
    justify-content: center;
  }
}
@media screen and (max-width: 767px) {
  .profile__info {
    max-width: 450px;
  }
}